.container{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap: 10px;
  width: 99%;
  margin-left: 10px;
  
}
.wrapper1{
    font-size: 1.5rem;
    width: 100%;
    height: 100%;
 color: rgba(252, 255, 255, 1.00);
    border-radius: 25px;
    background:rgba(0, 195, 154, 1.00);
}
.wrapper2{
    font-size: 1.5rem;
    width: 100%;
    height: 100%;
   color: rgba(15, 15, 13, 1.00);
    border-radius: 25px;
    background: rgba(255, 192, 60, 1.00);
}
.wrapper3{
    font-size: 1.5rem;
    width: 100%;
    height: 100%;
   color: rgba(252, 255, 255, 1.00);
    border-radius: 25px;
    background: rgba(254, 109, 121, 1.00);
}
.text{
    text-align: center;
margin-top: 50px;
}

@media screen and (max-width: 1024px){
    .container{
        width: 100%;
        height:18vmax;
    }
    .text{
        font-size: 2vh;
    }
    .wrapper1,.wrapper2,.wrapper3{
    width: 100%;
    height: 100%;
}

}
@media screen and (max-width:768px){
    .container{
        height: 22vmax;
        margin-top: -12px;
    }
    .wrapper1 ,.wrapper2,.wrapper3{
        width: 100%;
        height: 100%;
    }

    .text{
        font-size: 1.2rem;
        margin-top: 40px;
    }
}


@media screen and (max-width:480px){
  .container{
    display: inline-block;
  margin-top: 62px;
margin-left: 20px;
  }
    .text{
        margin: 10px;
        width: 100px;
        font-size: 1rem;
        margin-left: -1px;
  }
    .wrapper1 , .wrapper2, .wrapper3{ 
        width:100px;
        height: 60px;
        border-radius: 15px;
    }
 
}