
.menu_item{
  color: white;
  text-decoration: none;
  font-size: 2rem;
}
.burger_btn{
  position: absolute;
  width: 20px;
  height: 20px;
  right: 20px;
  top: 26px; 
  margin-right: 15px;
  margin-top: 5px;
  
}
.menu_bar{
  background-color: rgba(134, 132, 135, 1.00);
}

.text{
  position: absolute;
  margin-top: 10px;
margin-left: 25px;
  color: rgba(252, 255, 255, 1.00);
}

@media screen and (max-width:1024px){
  .text{
    margin-left: 20px;
  }
}

@media screen and (max-width:768px){
  .text{
    margin-left: 20px;
    margin-top: 8px;
  }
  .burger_btn{
    position: absolute;
    margin-top: 1px;
  }
}
@media screen and (max-width:480px){
  .burger_btn{
    position: absolute;
    margin-top: -5px;
    width: 20px;
    height: 20px;
  }
.text{
margin-top: 2px;
}


}